<template>
  <div id="home">
    <div v-if="!showLoading">
      <!---->
      <Header />
      <!---->
      <Sowing :sowing_list="sowing_list"></Sowing>
      <!---->
      <Nav :nav_list="nav_list" />
      <!---->
      <FlashSale :flash_sale_product_list="flash_sale_product_list" />
      <!---->
      <YouLike :you_like_product_list="you_like_product_list" />
      <!---->
      <MarkPage v-if="showBackStatus" :scrollToTop="scrollToTop" />
    </div>
    <van-loading
      v-else
      type="spinner"
      color="#75a342"
      style="
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translate(-50%);
      "
      >...</van-loading
    >
  </div>
</template>

<script>
//1. 
import { getHomeData } from "./../../service/api/index";
//2. 
import Header from "./components/header/Header";
//3. swiper
import Sowing from "./components/sowing/Sowing";
//4. nav
import Nav from "./components/nav/Nav";
//5. 
import FlashSale from "./components/flashSale/FlashSale";
//6. 
import YouLike from "./components/youLike/YouLike";
//7. 
import { showBack, animate } from "@/config/global";
//8. 
import MarkPage from "./components/markPage/MarkPage";
//9. 
import { mapMutations, mapState } from "vuex";
//10. 
import PubSub from "pubsub-js";
import { Toast } from "vant";
// 11.
import { addGoodsToCart } from "./../../service/api/index";

export default {
  name: "Home",
  data() {
    return {
      //
      sowing_list: [],
      //loading
      showLoading: true,
      //
      nav_list: [],
      //
      flash_sale_product_list: [],
      //
      you_like_product_list: [],
      //
      showBackStatus: false,
    };
  },
  created() {
    //
    this.reqData();
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  mounted() {
    PubSub.subscribe("homeAddToCart", (msg, goods) => {
      if (msg === "homeAddToCart") {
        // 
        if (this.userInfo.token) {
          // 
          this.dealGoodsAdd(goods);
        } else {
          // 
          this.$router.push("/login");
        }
      }
    });
  },
  components: {
    Header,
    Sowing,
    Nav,
    FlashSale,
    YouLike,
    MarkPage,
  },
  methods: {
    ...mapMutations(["ADD_GOODS"]),
    async reqData() {
      //
      let res = await getHomeData();
      // console.log(response);
      if (res.success) {
        this.sowing_list = res.data.list[0].icon_list;
        this.nav_list = res.data.list[2].icon_list;
        this.flash_sale_product_list = res.data.list[3].product_list;
        this.you_like_product_list = res.data.list[12].product_list;
        //
        this.showLoading = false;
        //
        showBack((status) => {
          this.showBackStatus = status;
        });
      }
    },
    // 
    scrollToTop() {
      // 
      let docB = document.documentElement || document.body;
      animate(docB, { scrollTop: "0" }, 400, "ease-out");
    },
    // 
    async dealGoodsAdd(goods) {
      // 2.1 
      let result = await addGoodsToCart(
        this.userInfo.token,
        goods.id,
        goods.name,
        goods.price,
        goods.small_image
      );
      console.log(result);
      if (result.success_code === 200) {
        this.ADD_GOODS({
          goodsId: goods.id,
          goodsName: goods.name,
          smallImage: goods.small_image,
          goodsPrice: goods.price,
        });
        // 
        Toast({
          message: "",
          duration: 800,
        });
      }
    },
  },
  beforeDestroy() {
    PubSub.unsubscribe("homeAddToCart");
  },
};
</script>

<style lang="less" scoped>
#home {
  width: 100%;
  // height: 300rem;
  height: 100%;
  background-color: #f5f5f5;
}
</style>